import Layout from "./Layout"
import { Form, Input, Button, Spin, Result } from "antd"
import { useDispatch } from "react-redux"
import { signup, signup_reset } from "../../store/actions/signup"
import { useSelector } from "react-redux"
import { useEffect } from "react"
import { Link } from "react-router-dom"

function Signup() {
  const dispatch = useDispatch()
  // 
  const { loading, loaded, success, message } = useSelector(
    state => state.signup
  )
  // 
  const handleOnFinish = value => {
    dispatch(signup(value))
  }
  // 
  const [form] = Form.useForm()
  //  loading
  const showLoading = () => {
    if (loading) return <Spin />
  }
  //  
  useEffect(() => {
    if (loaded && success) form.resetFields()
  }, [loaded, success, form])
  //  
  const showSuccess = () => {
    if (loaded && success) {
      return (
        <Result
          status="success"
          title=""
          extra={[
            <Button type="primary">
              <Link to="/signin"></Link>
            </Button>
          ]}
        />
      )
    }
  }
  //  
  const showError = () => {
    if (loaded && !success) {
      return <Result status="warning" title="" subTitle={message} />
    }
  }
  //  
  useEffect(() => {
    return () => {
      dispatch(signup_reset())
    }
  }, [dispatch])
  // 
  const signupForm = () => (
    <Form form={form} onFinish={handleOnFinish}>
      <Form.Item name="name" label="">
        <Input />
      </Form.Item>
      <Form.Item name="password" label="">
        <Input.Password />
      </Form.Item>
      <Form.Item name="email" label="">
        <Input />
      </Form.Item>
      <Form.Item>
        <Button type="primary" htmlType="submit">
          
        </Button>
      </Form.Item>
    </Form>
  )
  return (
    <Layout title="" subTitle="">
      {showLoading()}
      {showSuccess()}
      {showError()}
      {signupForm()}
    </Layout>
  )
}

export default Signup
