import Layout from "../core/Layout"
import { Col, Menu, Row, Descriptions, Typography } from "antd"
import { Link } from "react-router-dom"
import {
    ShoppingCartOutlined,
    UserOutlined,
    OrderedListOutlined
} from "@ant-design/icons"
import { isAuth } from "../../helpers/auth"


const { Title } = Typography
function AdminDashboard() {
    const {
        user: { name, email }
    } = isAuth()

    const adminLinks = () => (
        <>
            <Title level={5}></Title>
            <Menu>
                <Menu.Item>
                    <ShoppingCartOutlined />
                    <Link to="/create/category"></Link>
                </Menu.Item>
                <Menu.Item>
                    <UserOutlined />
                    <Link to="/create/product"></Link>
                </Menu.Item>
                <Menu.Item>
                    <OrderedListOutlined />
                    <Link to="/admin/order"></Link>
                </Menu.Item>
            </Menu>
        </>
    )
    const adminInfo = () => (
        <Descriptions title="" bordered>
            <Descriptions.Item label="">{name}</Descriptions.Item>
            <Descriptions.Item label="">{email}</Descriptions.Item>
            <Descriptions.Item label=""></Descriptions.Item>
        </Descriptions>
    )
    return <Layout title=" Dashboard">
        <Row>
            <Col span="4">{adminLinks()}</Col>
            <Col span="20">{adminInfo()}</Col>
        </Row>
    </Layout>
}

export default AdminDashboard
